.page {
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
  padding-top: 104px;
  display: flex;
  position: relative;
}

.layout {
  flex-direction: column;
  gap: 40px;
  margin-bottom: 40px;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.categories-list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.category {
  letter-spacing: -.02em;
  cursor: pointer;
  font-size: 16px;
  line-height: 1.12;
  transition-property: font-size, color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.075, .82, .165, 1);
}

.chosen-category {
  color: #4f2ee8;
  letter-spacing: -.32px;
  text-transform: uppercase;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Montserrat, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

.container {
  max-width: 480px;
  width: 100%;
  padding: 0 20px;
}

